<template>
    <div v-if="showModal" @click="closeModal">
        <div class="modal-page" @click.stop>
            <div class="modal-vue---close-icon" @click="closeModal">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 40 40" width="20" height="20" 
                    xml:space="preserve">
                    <path fill="#010110"
                        d="M8.7,7.6c-0.4-0.4-1-0.4-1.4,0C6.9,8,6.9,8.6,7.3,9l11,11l-11,11c-0.4,0.4-0.4,1,0,1.4c0.4,0.4,1,0.4,1.4,0 l11-11l11,11c0.4,0.4,1,0.4,1.4,0c0.4-0.4,0.4-1,0-1.4l-11-11L32,9c0.4-0.4,0.4-1,0-1.4c-0.4-0.4-1-0.4-1.4,0l-11,11L8.7,7.6z"
                        class="st0"></path>
                </svg>
            </div>
            <div class="contant_1">
                <div class="contant_1_left">
                    <h2>
                        {{ modalTitle }}
                        <span>{{ modalDate }}</span>
                    </h2>
                    <img class="modal-image" :src="modalImage" alt="" />

                    <h3>{{ modalSubtitle }}</h3>
                    <br class="clear bottomClear" />
                    <p>{{ modalText }}</p>
                    <p class="quoteBar">{{ modalQuote }}</p>
                    <p>{{ modalTextContinued }}</p>
                </div>
                <div class="sideBar">
                    <div class="sideBarListBox">
                        <ul>
                            <li><a href="#">What is Lorem Ipsum?</a></li>
                            <li><a href="#">Why Do We Use It?</a></li>
                            <li><a href="#">Where Does It Come From?</a></li>
                            <li><a href="#">Where Can I Get Some?</a></li>
                            <li><a href="#">There are many variations of passages of Lorem Ipsum available</a></li>
                            <li><a href="#">Alteration In Some Form</a></li>
                            <li><a href="#">If you are going to use a passage</a></li>
                            <li><a href="#">You Need To Be Sure There</a></li>
                            <li><a href="#">The generated Lorem Ipsum is</a></li>
                        </ul>
                    </div>
                    <!-- <div class="sideBarItem">
                    <h3>Blogroll</h3>
                    <ul>
                        <li><a href="#">Category One</a></li>
                        <li class="cat002"><a href="#">There are many</a></li>
                        <li class="cat003"><a href="#">Lorem Ipsum available</a></li>
                        <li class="cat003"><a href="#">If you are going</a></li>
                        <li class="cat003"><a href="#">All the Lorem Ipsum</a></li>
                        <li class="cat003"><a href="#">200 Latin words</a></li>
                        <li class="cat003"><a href="#">It uses a dictionary</a></li>
                        <li class="cat003"><a href="#">Lorem ipsum dolor</a></li>
                        <li class="cat003"><a href="#">Latin professor at</a></li>
                    </ul>
                </div> -->
                </div>
                <br class="clear" />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ModalPage',
    props: {
        showModal: Boolean,
        modalTitle: String,
        modalSubtitle: String,
        modalText: String,
        modalTextContinued: String,
        modalQuote: String,
        modalImage: String,
        modalDate: String,
    },
    methods: {
        closeModal() {
            this.$emit('close');
        },
    },
    watch: {
        showModal(newValue) {
            if (newValue) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = '';
            }
        },
    },
};
</script>


<style>
@import "/src/assets/css/style.css";
</style>
