<template>
    <div>
        <NavbarPage />
        <div class="grid">
               <section class="content item1">
                <h1 class="text-white">𝐂𝗋α𝖿𝗍𝗂𐓣𝗀 𝐃𝗂𝗀𝗂𝗍αᥣ 𝐃𝗋𝖾αꭑ𝗌:&nbsp;  
                   <span class="typewriter"></span></h1>
                <p class="text-white">Welcome to my digital realm! I code, craft web wonders, wander the world, and game with gusto. This isn't just a portfolio—it's a snapshot of my passion for tech, travel, and gaming. Code becomes poetry, travels turn into tales, and games transform into adventures. Ready to join me on this creative journey?</p>

            </section>
            <section class="blogs-card item2"  @click="openModal(2)"   >
                <h2>Hover the blogs-cards</h2>
            </section>

            <section class="blogs-card item3"></section>

            <section class="blogs-card item4" @click="openModal(4)" >
                <h2> &ldquo;Travel feeds my code, code fuels my travels, and games? They're the bridges I build between
                    them, inviting others to explore both landscapes - the ones on screens and the ones etched in my
                    wanderlust-filled heart. &rdquo;</h2>
            </section>

            <section class="blogs-card item5" @click="openModal(5)" >
                <h2>because interactions make it more fun</h2>
            </section>
             <section class="blogs-card item6"></section>
            <section class="blogs-card item7"></section>

            <section class="blogs-card item8" @click="openModal(8)" >
                <img src="../../assets/img/create a image for mixture of hobbies of coding, gaming and travelling[46].gif"
                    alt="Description of the image">
            </section>

            <section class="blogs-card item9"></section>

            <section class="blogs-card item10"></section>

            <section class="blogs-card item11" @click="openModal(11)" >
                <h2>add a :focus state too!</h2>
            </section>

            <ModalPage :show-modal="isModalVisible" :modal-title="modalTitle" :modal-text="modalText" :modalImage="modalImage" @close="closeModal" />
        </div>
    </div>
</template>

<script>
import NavbarPage from '../NavbarPage/NavbarPage.vue';
import ModalPage from './ModalPage.vue';

export default {
    name: 'SkillsPage',
    components: {
        NavbarPage,
        ModalPage,
    },
    data() {
        return {
            isModalVisible: false,
            modalTitle: '',
            modalText: '',
            modalImage: '',
        };
    },
    methods: {
        openModal(sectionNumber) {
            // Hide cursor when a section is clicked
            document.body.style.cursor = 'none';

            switch (sectionNumber) {
                case 2:
                    this.modalTitle = 'Blogs Section';
                    this.modalText = 'This is the modal text for the Blogs section.';
                    this.modalImage = require('../../assets/img/hi.png');
                    break;
                case 4:
                    this.modalTitle = 'Interactions Section';
                    this.modalText = 'This is the modal text for the Interactions section.';
                    this.modalImage = require('../../assets/img/hi.png');
                    break;
                case 5:
                    this.modalTitle = 'Interactions Section';
                    this.modalText = 'This is the modal text for the Interactions section.';
                    this.modalImage = require('../../assets/img/hi.png');
                    break;
                case 8:
                    this.modalTitle = 'Image Section';
                    this.modalText = 'This is the modal text for the Image section.';
                    this.modalImage = require('../../assets/img/hi.png');
                    break;
                case 11:
                    this.modalTitle = 'Focus State Section';
                    this.modalText = 'This is the modal text for the Focus State section.';
                    this.modalImage = require('../../assets/img/Animated.mp4');
                    break;
                // Add more cases for other sections if needed
            }

            this.isModalVisible = true;

            // Show cursor after 2 seconds
            setTimeout(() => {
                document.body.style.cursor = 'auto';
            }, 2000);
        },

        closeModal() {
            this.isModalVisible = false;
        },

        handleHoverEffect(itemNumber) {
            const cardItems = document.querySelectorAll('.blogs-card');
            cardItems.forEach((item, index) => {
                if (index === itemNumber) {
                    item.classList.add('active');
                } else {
                    item.classList.remove('active');
                }
            });
        },
    },
};
</script>

