<template>
  <div>
    <!-- Navbar -->
    <nav>
        <div class="navbar navbar-expand-lg pt-4">
            <div class="container-fluid">
                <!-- Navbar Toggler -->
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                    aria-label="Toggle navigation">
                    <img src="@/assets/bars-solid.svg" style="height: 25px;" alt="list">
                </button>
                <!-- Navbar Items -->
                <div class="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                    <ul  class="navbar-nav justify-content-between border-top border-2 text-center nav-length">
                        <!-- Loop through navbar items -->
                        <li v-for="item in navbarItems" :key="item.id" class="nav-item">
                            <router-link v-bind:to="item.link" class="nav-link border-hover py-3 text-white">{{ item.text
                            }}
                            </router-link>
                        </li>
                        <li class="nav-item">
                          <a  class="nav-link border-hover py-3 text-white"  href="https://drive.google.com/file/d/1tJYgc_I4pa6SzbF2gBgfQVxC3pX42rEK/view?usp=sharing" target="_blank">Resume</a>
                        </li>
                        <!-- name-tag Button with Hover Effect -->
                        <li class="nav-item" @mouseover="toggleLayer(true)" @mouseout="toggleLayer(false)">
                            <router-link to="#/" id="name-tag" class="nav-link my-2 px-4 text-white button-57"
                                @mouseover="toggleLayer(true)">
                                <img class="me-3" src="@/assets/person-circle.svg" alt="person-circle icon">
                                <span class="text">Thummala Jaswanth</span>
                                <span class="alternate-text">Web Developer</span>
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </nav>
    </div>
</template>

<script>
export default {
    name: 'NavbarPage',
    props: {
        msg: String
    },
    data() {
        return {
            showLayer: false,
            navbarItems: [
                { id: 1, text: 'Home', link: '/' },
                { id: 2, text: 'About Me', link: '/about-me' },
                { id: 3, text: 'Skills & Projects', link: '/SkillsXProjects' },
                { id: 5, text: 'Blog', link: '/MyBlogs' }
            ],
        }
    },

     methods: {
        changeText(newText) {
            this.$refs.textContents.innerText = newText;
        },
        toggleLayer(value) {
            this.showLayer = value;
        }
    }
}
</script>



