<template>
  <div>
    <NavbarPage />
    <h1 class="text-white text-center p-4">Skill Proficiency</h1>

   <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
        <div class="carousel-inner">
          <div v-for="(personGroup, index) in formattedPeople" :key="index"
            :class="{ 'carousel-item': true, 'active': index === 0 }">

            <div class="row justify-content-around">
              <div v-for="(person, innerIndex) in personGroup" :key="innerIndex" class="skills-card w-100">
              <div class="imgBx">
                <img :src="person.image" alt="images">
              </div>
              <div class="details">
                <h2>{{ person.lang }}</h2>
                <div class="progress-container">
                  <div class="progress-bar-container">
                    <div class="progress-bar" :style="{ width: person.progress + '%' }"></div>
                  </div>
                  <div class="progress-label">{{ person.progress }}%</div>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>

      <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Previous</span>
      </button>

      <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Next</span>
      </button>
    </div>

    <div class="cw-format-container">
        <h1 class="text-white text-center p-5">Creative Works</h1>
        <div class="cw-courses_box">
          <div
            v-for="(course, index) in courses"
            :key="index"
            class="cw-courses_item"
            @mouseenter="hoveredIndex = index"
            @mouseleave="hoveredIndex = -1"
          >
            <a :href="course.link" class="cw-courses-item_link">
              <div class="cw-courses-item_bg"></div>
              <div v-if="hoveredIndex !== index">
                <div class="cw-courses-item_title">
                  {{ course.title }}
                </div>
                <div class="cw-courses-item_date-box">
                  Start:
                  <span class="cw-courses-item_date">{{ course.startDate }}</span>
                </div>
              </div>
              <div v-else>
                <div class="cw-courses-item_title description">
                  {{ course.description }}
                </div>
                <div class="cw-courses-item_lang">
                  Languages:
                <span class="cw-courses-item_date">
                   {{ course.languages.join(', ') }}
                </span>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import NavbarPage from '../NavbarPage/NavbarPage.vue';

export default {
  name: 'SkillsPage',
  props: {
    msg: String
  },
  components: {
    NavbarPage,
  },
  data() {
    return {
      people: [
        {
          lang: 'C++',
          image: require('../../assets/img/C+lang.png'),
          progress: 50
        },
        {
          lang: 'HTML',
          image: require('../../assets/img/html.png'),
          progress: 75
        },
        {
          lang: 'CSS',
          image: require('../../assets/img/css.png'),
          progress: 30
        },
        {
          lang: 'JavaScript',
          image: require('../../assets/img/js.png'),
          progress: 75
        },
        {
          lang: 'Vue',
          image: require('../../assets/img/vue.png'),
          progress: 30
        },
        {
          lang: 'Python',
          image: require('../../assets/img/python.png'),
          progress: 75
        },
        // Add more people as needed
      ],
      itemsPerRow:3,
       courses: [
        // ... your existing courses
        {
          title: 'E-commerce Website',
          link: '#',
          startDate: '01.01.2023',
          description: 'Developed an e-commerce website using HTML, CSS, and JavaScript. Implemented product listing, cart functionality, and checkout process.',
          languages: ['HTML', 'CSS', 'JavaScript'],
        },
        {
          title: 'Portfolio Website',
          link: '#',
          startDate: '15.02.2023',
          description: 'Designed and developed a personal portfolio website showcasing skills, projects, and contact information. Implemented responsive design for various devices.',
          languages: ['HTML', 'CSS', 'JavaScript'],
        },
        {
          title: 'Python Data Analysis Tool',
          link: '#',
          startDate: '01.03.2023',
          description: 'Created a data analysis tool in Python for processing and visualizing large datasets. Used Pandas and Matplotlib for data manipulation and visualization.',
          languages: ['Python'],
        },
        {
          title: 'Chatbot Development',
          link: '#',
          startDate: '15.04.2023',
          description: 'Developed a chatbot using Python and natural language processing (NLP) libraries. Implemented conversation flows and integrated with messaging platforms.',
          languages: ['Python'],
        },
        {
          title: 'Vue.js Task Management App',
          link: '#',
          startDate: '01.05.2023',
          description: 'Built a task management web application using Vue.js. Implemented features like task creation, editing, and deletion.',
          languages: ['HTML', 'CSS', 'JavaScript', 'Vue.js'],
        },
        {
          title: 'Real-time Dashboard with Vue.js',
          link: '#',
          startDate: '15.06.2023',
          description: 'Developed a real-time dashboard using Vue.js to display live data. Implemented data visualization components and integrated with WebSocket for real-time updates.',
          languages: ['HTML', 'CSS', 'JavaScript', 'Vue.js'],
        },
        {
          title: 'C++ Game Development',
          link: '#',
          startDate: '01.07.2023',
          description: 'Developed a simple 2D game using C++. Implemented game mechanics, graphics rendering, and user interactions.',
          languages: ['C++'],
        },
        {
          title: 'Image Processing with C++',
          link: '#',
          startDate: '15.08.2023',
          description: 'Implemented image processing algorithms using C++. Worked on tasks such as image filtering, edge detection, and color manipulation.',
          languages: ['C++'],
        },
        // Add more freelancer projects as needed
      ],
      hoveredIndex: -1,
    };
  },
   computed: {
    formattedPeople() {
      const formattedPeople = [];
      for (let i = 0; i < this.people.length; i += this.itemsPerRow) {
        formattedPeople.push(this.people.slice(i, i + this.itemsPerRow));
      }
      return formattedPeople;
    },
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize(); // Initial call to set the correct number of items
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      // Adjust the number of items per row based on screen width
      this.itemsPerRow = window.innerWidth <= 828 ? 1 : 3;
    },
  },
};
</script>
