                            <img class="w-100 about-me" src="../../assets/img/laptop-teaching.png" />
                        </div>
                        <div class="col-lg-6 text-right" style="margin-right: -60px;">
                            <div class="mt-5">
                                <h1 class="mb-4 about">{{ aboutMe.title }}</h1>
