                                </ul>
                                <a href="#MoreDetails" class="details-btn">Find more details</a>
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            <section class="timeline" id="MoreDetails">
                <div class="container">
                    <div v-for="(item, index) in timelineItems" :key="index" class="timeline-item">
                        <div class="timeline-img"></div>

                        <div :class="['timeline-content', 'timeline-card', item.animationClass]">
                            <template v-if="item.type === 'text'">
                                <h4>{{ item.title }}</h4>
                                <div class="date">{{ item.date }}</div>
                                <p v-html="item.content"></p>
                                <p >{{ item.cgpa }}</p>
                                 <p v-html="item.readMoreText" v-if="item.expanded && item.readMoreText"></p>
                    <p><a @click="toggleReadMore(item)" class="bnt-more" href="javascript:void(0)">
                      {{ item.expanded ? 'Read Less' : 'Read More' }}
                    </a></p>
                            </template>

                            <template v-else-if="item.type === 'card'">
                                <div class="timeline-img-header"
                                    :style="{ backgroundImage: `url('${item.cardImageUrl}')` }">
                                    <h4>{{ item.title }}</h4>
                                </div>
                                <div class="date">{{ item.date }}</div>
                                <p v-html="item.content"></p>
                                <p>{{ item.cgpa }}</p>         
                                 <p v-html="item.readMoreText" v-if="item.expanded && item.readMoreText"></p>
