                    readMoreText: 'I successfully completed my schooling, acquiring a solid foundation of knowledge and skills that have equipped me for the next chapter of my academic or professional journey.',
                },
                // {
                //     type: 'quote',
                //     animationClass: 'js--fadeInRight',
                //     date: 'April 2016',
                //     quote:"Let’s do what we love, and let’s do a lot of it.",
                  
                // },
                {
                    type: 'card',
                    title: 'Secondary High School',
                    date: 'June 2016 – April 2018',
                    content: 'Narayana Junior College, Secunderabad, India',
                    animationClass: 'js--fadeInRight',
                    cardImageUrl: 'https://img.freepik.com/free-vector/hand-drawn-scientific-formulas-chalkboard-background_23-2148500604.jpg?size=626&ext=jpg&ga=GA1.1.2116175301.1701475200&semt=ais',
                    cgpa: 'CGPA: 9.12',
                    readMoreText: 'I navigated through senior secondary education, excelling in both 11th and 12th grades. This academic journey enriched my knowledge base and cultivated essential skills, laying a strong foundation for my future endeavors. With successful completion, I am poised for the next chapter of my educational or professional pursuits.',
                },
                {
                    type: 'card',
                    title: 'B.Tech in Computer Science and Engineering',
                    date: 'August 2018 – July 2022',
                    content: 'Lovely Professional University, Punjab, India',
                    animationClass: 'js--fadeInLeft',
                    cardImageUrl: 'https://images.unsplash.com/photo-1525921429624-479b6a26d84d?crop=entropy&cs=srgb&fm=jpg&ixid=M3wzMjM4NDZ8MHwxfHJhbmRvbXx8fHx8fHx8fDE3MDY1MTk1OTh8&ixlib=rb-4.0.3&q=85',
                    cgpa: 'CGPA: 6.97',
                    readMoreText: 'I have successfully completed my Bachelor of Technology(B.Tech) in Computer Science and Engineering, specializing in core web development.This accomplishment has solidified my proficiency in creating dynamic and efficient web solutions, laying the groundwork for my endeavors in the field.',
                },
            ],
        };
    },
    methods: {
        toggleReadMore(item) {
            item.expanded = !item.expanded;
        },
    },
    mounted() {
        const sr = ScrollReveal();

        const revealAnimation = (element, origin, duration, delay) => {
            sr.reveal(element, {
                origin,
                distance: window.innerWidth < 768 ? '20px' : '300px',
                easing: 'ease-in-out',
                duration,
                delay,
            });
        };

        revealAnimation('.js--fadeInLeft', 'left', 800, 100);
        revealAnimation('.js--fadeInRight', 'right', 800, 100);
    },
};
</script>
